
import {
	defineComponent,
} from 'vue'
import { useLogger } from 'vue-logger-plugin'
import { useI18n } from 'vue-i18n'

export default defineComponent({
	name: 'Navigation',
	components: {
	},
	props: {
	},
	setup() {
		const log = useLogger()
		const { t } = useI18n()

		return {
			log,
			t,
		}
	},
})
